                    background: rgb(228, 228, 228);
                    background: linear-gradient(0deg, rgba(228, 228, 228, 1) 46%, rgba(238, 238, 238, 1) 56%);

                }


                .cell-status {

                    margin-left: auto;
                    width: 80px;

                }

