                    width: 70px;

                    white-space: nowrap;

                    overflow: hidden;

                    text-overflow: ellipsis;

                }

                .cell-action {
                    width: 30px;
                }


                .dropzone-text-container {

                    position: absolute;
                    top: 39px;
                    left: 0;
                    right: 0;
                    bottom: 0;

                    overflow-y: auto;

                    font: normal 11px Verdana, sans-serif;

                    ul {
                        margin: 0;
